</docs>

<template>
	<li class="action" :class="{ 'action--disabled': disabled }">
		<span :class="{
				'action-input-picker--disabled': disabled,
				'action-input--visible-label': labelVisible && label,
			}"
			class="action-input"
			@mouseleave="onLeave">
			<span class="action-input__icon-wrapper">
				<!-- @slot Manually provide icon -->
				<slot name="icon">
					<span :class="[isIconUrl ? 'action-input__icon--url' : icon]"
						:style="{ backgroundImage: isIconUrl ? `url(${icon})` : null }"
						:aria-hidden="ariaHidden"
						class="action-input__icon" />
				</slot>
			</span>

			<!-- form and input -->
			<form ref="form"
				class="action-input__form"
				:disabled="disabled"
				@submit.prevent="onSubmit">

				<NcDatetimePicker v-if="datePickerType"
					ref="datetimepicker"
					:value="value"
					:placeholder="text"
					:disabled="disabled"
					:type="datePickerType"
					:input-class="['mx-input', { focusable: isFocusable }]"
					class="action-input__datetimepicker"
					v-bind="$attrs"
					@input="onInput"
					@change="onChange" />

				<NcDateTimePickerNative v-else-if="isNativePicker"
					:id="idNativeDateTimePicker"
					:value="value"
					:type="nativeDatePickerType"
					:input-class="{ focusable: isFocusable }"
					class="action-input__datetimepicker"
					v-bind="$attrs"
					@input="$emit('input', $event)"
					@change="$emit('change', $event)" />

				<NcSelect v-else-if="isMultiselectType"
					:value="value"
					:placeholder="text"
					:disabled="disabled"
					:append-to-body="false"
					:input-class="{ focusable: isFocusable }"
					class="action-input__multi"
					v-bind="$attrs"
					v-on="$listeners" />

				<template v-else>
					<div class="action-input__container">
						<label v-if="label"
							class="action-input__text-label"
							:class="{ 'action-input__text-label--hidden': !labelVisible }"
							:for="inputId">
							{{ label }}
						</label>
						<div class="action-input__input-container">
							<NcPasswordField v-if="type==='password'"
								:id="inputId"
								:value="value"
								:label="text"
								:disabled="disabled"
								:input-class="{ focusable: isFocusable }"
								trailing-button-icon="arrowRight"
								:show-trailing-button="showTrailingButton && value !== '' && !disabled"
								v-bind="$attrs"
								v-on="$listeners"
								@trailing-button-click="$refs.form.requestSubmit()"
								@input="onInput"
								@change="onChange" />

							<NcColorPicker v-else-if="type==='color'"
								:id="inputId"
								:value="value"
								class="colorpicker__trigger"
								v-bind="$attrs"
								v-on="$listeners"
								@input="onInput"
								@submit="$refs.form.requestSubmit()">
								<button :style="{'background-color': value}"
									class="colorpicker__preview"
									:class="{ focusable: isFocusable }" />
							</NcColorPicker>

							<NcTextField v-else
								:id="inputId"
								:value="value"
								:label="text"
								:disabled="disabled"
								:input-class="{ focusable: isFocusable }"
								:type="type"
								trailing-button-icon="arrowRight"
								:show-trailing-button="showTrailingButton && value !== '' && !disabled"
								v-bind="$attrs"
								v-on="$listeners"
								@trailing-button-click="$refs.form.requestSubmit()"
								@input="onInput"
								@change="onChange" />
						</div>
					</div>
				</template>
			</form>
		</span>
	</li>
</template>

<script>
import NcDatetimePicker from '../NcDatetimePicker/index.js'
import NcDateTimePickerNative from '../NcDateTimePickerNative/index.js'
import NcPasswordField from '../NcPasswordField/index.js'
import NcSelect from '../NcSelect/index.js'
import NcTextField from '../NcTextField/index.js'
import ActionGlobalMixin from '../../mixins/actionGlobal.js'
import GenRandomId from '../../utils/GenRandomId.js'

export default {
	name: 'NcActionInput',

	components: {
		NcDatetimePicker,
		NcDateTimePickerNative,
		NcPasswordField,
		NcSelect,
		NcTextField,
	},

	mixins: [ActionGlobalMixin],

	props: {
		/**
		 * id attribute of the checkbox element
		 */
		id: {
			type: String,
			default: () => 'action-' + GenRandomId(),
			validator: id => id.trim() !== '',
		},
		/**
		 * id attribute of the text input element
		 */
		inputId: {
			type: String,
			default: () => 'action-input-' + GenRandomId(),
			validator: id => id.trim() !== '',
		},
		/**
		 * Icon to show with the action, can be either a CSS class or an URL
		 */
		icon: {
			type: String,
			default: '',
		},
		/**
		 * type attribute of the input field
		 */
		type: {
			type: String,
			default: 'text',
			validator(type) {
				return ['date', 'datetime-local', 'month', 'multiselect',
					'number', 'password', 'search', 'tel',
					'text', 'time', 'url', 'week', 'color',
					'email'].indexOf(type) > -1
			},
		},
		/**
		 * id attribute for the native date time picker
		 */
		idNativeDateTimePicker: {
			type: String,
			default: 'date-time-picker_id',
		},
		/**
		 * Flag to use a native date time picker
		 */
		isNativePicker: {
			type: Boolean,
			default: false,
		},
		/**
		 * The visible input label for accessibility purposes.
		 */
		label: {
			type: String,
			default: null,
		},
		/**
		 * If you want to hide the label just above the
		 * input field, pass in `false` to this prop.
		 */
		labelVisible: {
			type: Boolean,
			default: true,
		},
		/**
		 * value attribute of the input field
		 */
		value: {
			type: [String, Date, Number, Array],
			default: '',
		},
		/**
		 * disabled state of the input field
		 */
		disabled: {
			type: Boolean,
			default: false,
		},
		/**
		 * aria-label attribute of the input field
		 */
		ariaLabel: {
			type: String,
			default: '',
		},
		/**
		 * aria-hidden attribute for the icon slot
		 */
		ariaHidden: {
			type: Boolean,
			default: null,
		},
		/**
		 * Attribute forwarded to the underlining NcPasswordField and NcTextField
		 */
		showTrailingButton: {
			type: Boolean,
			default: true,
		},
	},

	emits: [
		'input',
		'submit',
		'change',
		'update:value',
	],

	computed: {
		isIconUrl() {
			try {
				return new URL(this.icon)
			} catch (error) {
				return false
			}
		},

		isMultiselectType() {
			return this.type === 'multiselect'
		},

		nativeDatePickerType() {
			switch (this.type) {
			case 'date':
			case 'month':
			case 'time':
			case 'week':
			case 'datetime-local':
				return this.type
			}
			return false
		},

		datePickerType() {
			if (!this.isNativePicker) {
				switch (this.type) {
				case 'date':
				case 'month':
				case 'time':
					return this.type

				case 'datetime-local':
					return 'datetime'
				}
			}
			return false
		},
		/**
		 * determines if the action is focusable
		 *
		 * @return {boolean} is the action focusable ?
		 */
		isFocusable() {
			return !this.disabled
		},
	},

	methods: {
		// closing datepicker popup on mouseleave = unfocus
		onLeave() {
			if (this.$refs.datetimepicker && this.$refs.datetimepicker.$refs.datepicker) {
				this.$refs.datetimepicker.$refs.datepicker.closePopup()
			}
		},
		onInput(event) {
			/**
			 * Emitted on input events of the text field
			 *
			 * @type {Event|Date}
			 */
			this.$emit('input', event)
			/**
			 * Emitted when the inputs value changes
			 * ! DatetimePicker only send the value
			 *
			 * @type {string|Date}
			 */
			this.$emit('update:value', event.target ? event.target.value : event)
		},
		onSubmit(event) {
			event.preventDefault()
			event.stopPropagation()
			if (!this.disabled) {
				/**
				 * Emitted on submit of the input field
				 *
				 * @type {Event}
				 */
				this.$emit('submit', event)
			} else {
				// ignore submit
				return false
			}
		},
		onChange(event) {
			/**
			 * Emitted on change of the input field
			 *
			 * @type {Event}
			 */
			this.$emit('change', event)
		},
	},
}
</script>

<style lang="scss" scoped>
@import '../../assets/inputs';
@import '../../assets/action';
@include action-active;
@include action--disabled;

$input-margin: 4px;

.action-input {
	display: flex;
	align-items: flex-start;

	width: 100%;
	height: auto;
	margin: 0;
	padding: 0;

	cursor: pointer;
	white-space: nowrap;

	color: var(--color-main-text);
	border: 0;
	border-radius: 0; // otherwise Safari will cut the border-radius area
	background-color: transparent;
	box-shadow: none;

	font-weight: normal;

	&__icon-wrapper {
		display: flex;
		align-self: center;
		align-items: center;
		justify-content: center;

		&:deep(.material-design-icon) {
			width: $clickable-area;
			height: $clickable-area;
			opacity: $opacity_full;

			.material-design-icon__svg {
				vertical-align: middle;
			}
		}
	}

	& > span {
		cursor: pointer;
		white-space: nowrap;
	}

	&__icon {
		min-width: 0; /* Overwrite icons*/
		min-height: 0;
		/* Keep padding to define the width to
			assure correct position of a possible text */
		padding: #{math.div($clickable-area, 2)} 0 #{math.div($clickable-area, 2)} $clickable-area;

		background-position: #{$icon-margin} center;
		background-size: $icon-size;
	}

	// Forms & text inputs
	&__form {
		display: flex;
		align-items: center;
		flex: 1 1 auto;

		margin: $input-margin 0;
		padding-right: $icon-margin;
	}

	&__container {
		width: 100%;
	}

	&__input-container {
		display: flex;

		.colorpicker {
			&__trigger,
			&__preview {
				width: 100%;
			}

			&__preview {
				width: 100%;
				height: 36px;
				border-radius: var(--border-radius-large);
				border: 2px solid var(--color-border-maxcontrast);
				box-shadow: none !important;
			}
		}
	}

	&__text-label {
		padding: 4px 0;
		display: block;

		&--hidden {
			position: absolute;
			left: -10000px;
			top: auto;
			width: 1px;
			height: 1px;
			overflow: hidden;
		}
	}

	&__datetimepicker {
		width: 100%;

		:deep(.mx-input) {
			margin: 0;
		}
	}

	&__multi {
		width: 100%;
	}
}

// if a form is the last of the list
// add the same bottomMargin as the right padding
// for visual balance
li:last-child > .action-input {
	padding-bottom: $icon-margin - $input-margin;
}

// same for first item
li:first-child > .action-input:not(.action-input--visible-label) {
	padding-top: $icon-margin - $input-margin;
}

</style>
